/**
 * @pageName CollectPayment
 * @slotType dashboard menu plugin
 */
export type CollectPaymentMenuParams = CollectPaymentMenuOptionParams;

/** @internal */
export type CollectPaymentMenuOptionParams = {
  orderId: string;
  /** @deprecated Use onSuccess() instead */
  onSuccessCallback(): Promise<void>;
  onSuccess(): Promise<void>;
};

/**
 * @pageName CollectPayment
 * @slotType dashboard plugin
 */
export type CollectPaymentModalParams =
  CollectPaymentMenuOnBeforePayModalSlotProps;

/** @internal */
export type CollectPaymentMenuOnBeforePayModalSlotProps = {
  orderId: string;
  onSuccess(): Promise<void>;
  onCancel(): void;
  menuOption:
    | {
        key:
          | 'CHARGE_WITH_CREDIT_CARD'
          | 'RECORD_ORDER_MANUAL_PAYMENT'
          | 'CHARGE_WITH_INVOICE'
          | 'CAPTURE_AUTHORIZED_PAYMENT'
          | 'CREATE_AUTHORIZED_PAYMENT';
        componentId?: undefined;
      }
    | {
        key: 'EXTENSION';
        componentId: string;
      };
};
