/**
 * @pageName Orders
 * @slotType dashboard menu plugin
 */
export type OrderItemsBulkMoreActionsMenuParams =
  OrdersListBulkActionExtensionProps;
/** @internal */
export type OrdersListBulkActionExtensionProps = {
  // An array of IDs representing the orders that have been selected.
  selectedIds: string[];
  // An array of IDs representing the orders that have been unchecked. Applicable only when the "Select All" option was selected.
  uncheckedIds: string[];
  // A boolean value indicating whether the "Select All" option was selected.
  isSelectAll: boolean;
  // The total number of orders that were selected.
  total: number;
  // Callback that notifies the host after successful handling of the flow associated with the new menu item.
  onSuccess(): Promise<void>;
};
